/* Buttons */
.button{
  font-family: 'Karla', sans-serif !important;
  font-weight: 700;
  color: white;
}

/*LINKS*/
a{
  cursor: pointer;
  color: rgba(0, 0, 0, 0.7);
}

a:visited{
  color: rgba(0, 0, 0, 0.7);
}

a:hover{
  font-weight: 700;
}

.a-clean{
  cursor: pointer;
  text-decoration: none;
  color: white;
  font-weight: 500;
}

.a-clean:visited{
  color: white;
}


/* HOME */ /*TODO* BYT UT BILD*/
.home{
  background-image: url(/FrolichConsulting/static/media/background.1e0b19a0.png);
  height: 100vh;
  text-align: center;
  margin: 0;
  padding: 0;
}

.home h1{
  color: white;
  margin-bottom: 0;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}

@media (max-width: 1000px){
  .block{
    display: block !important;
  }
}

/* Work */
.work{
  background-color: #F9F9F9;
}

/* About */
.about .img-profile{
  height: 100%;
  width: 100%;
  max-width: 450px;
}

@media (max-width: 768px){
  .ml-2{
    margin-left: 0 !important;
  }
}

/* Utils */
.center-vertical{
  width: 50%;
  margin: 0 auto;
}

.centered-content{
  margin-right: auto;
  margin-left: auto;
  width: 70%;
}

@media (max-width: 768px){
  .centered-content{
    width: 90%;
  }
  .center-vertical{
    width: 90%;
  }
}

.center-horizontal{
  padding-top: 40%;
}

.bottom-aligned{
  width: 100%;
  position: absolute;
  bottom: 20px;
}

.flex{
  display: flex;
}

.align-items{
  align-items: center;
}

.ml-1{
  margin-left: 1em;
}

.ml-2{
  margin-left: 2em;
}

.mb-2{
  margin-bottom: 2em;
}

.mt-2{
  margin-top: 2em;
}

.w-40{
  width: 40%;
}

@media (max-width: 768px){
  .w-40{
    width: 100%;
  }
}

.hamburgerButton{
  display: flex;
  flex-direction: column;
  height: 24px;
  width: 30px;
  background: transparent;
  justify-content: space-around;
  border: none;
  cursor: pointer;
  padding: 0;
}

.hamburgerButton:focus{
  outline: none;
}

.hamburgerButton-line{
  height: 3px;
  width: 30px;
  background: white;
}

@media (min-width: 769px){
  .hamburgerMenu-button{
    display: none !important;
  }
}

.navbar{
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 68px;
}

.navbar-navigation{
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.navbar-logo{
  margin-right: auto;
  margin-left: 1rem;
}

.navbar-logo img{
  heigh: 80%;
  width: 80%;
}

.navbar-navigation ul{
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar-navigation-items li{
  padding: 0 1.5rem;
}

.navbar-navigation-items a{
  color: white;
  text-decoration: none;
}

.navbar-navigation-items a:hover{
  font-weight: 700;
}

/*TODO*/
.navbar-navigation-items a:active{
}

@media (max-width: 768px){
  .navbar-navigation-items{
    display: none;
  }
}

@media (min-width: 769px){
  .navbar-logo{
    margin-left: 0;
  }
}

.active{
  box-shadow: 0 -0.5em #4200FF inset;
  font-weight: 700;
}

.drawer{
  height: 100%;
  width: 70%;
  background: white;
  box-shadow: 3px 0px 5px rgba(0, 0, 0, 0.25);
  position: fixed;
  top: 0;
  left: 0;
  max-width: 300px;
  z-index: 200;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: -webkit-transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
  transition: -webkit-transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0), -webkit-transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
}

.drawer ul{
  list-style: none;
  display: flex;
  flex-direction: column;
}

/*TODO: DEF BLACKS*/
.drawer a{
  text-decoration: none;
  color: black;
}

.drawer li{
  margin: 0.5rem 0;
}

.drawer.open{
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

@media (min-width: 769px){
  .drawer{
    display: none;
  }
}

.backdrop{
  top: 0;
  left: 0;
  z-index: 100;
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
}

@media (min-width: 769px){
  .backdrop{
    display: none;
  }
}

.card-bg{
  background-color: white !important;
  border-radius: 4px;
}

.card-text{
  padding: 1em;
  margin-left: 1%;
}

.card-media img{
  max-width: 90vw;
  width: 400px;
  height: 100%;
  border-radius: 4px 0 0 4px;
  object-fit: cover;
}

.card-text img{
  top: 8px;
  position: relative;
  margin-right: 4px;
}

@media (max-width: 1000px){
  .card-media img{
    width: 100%;
    border-radius: 4px 4px 0 0;
  }

  .card-text{
    margin-left: 0 !important;
  }
}

